/**
 * External imports
 */
import { useMemo } from 'react';
import { useQuery } from 'react-query';
import queryString from 'query-string';
import axios from 'axios';

/**
 * Internal imports
 */
import { useGeolocation } from '~/libs/hooks';
import { useToast } from '~/components/common';

const appId = 'c59ada2d7a63bb6e24deee7e56c81bac';

export interface WeatherType {
  dt: number;
  temp: number | { day: number };
  uvi: 10;
  weather: {
    description: string;
    icon: string;
    id: number;
    main: string;
  }[];
}

export const useWeather = () => {
  const { currentPosition } = useGeolocation();
  const { appendToast } = useToast();

  const apiPath = useMemo(() => {
    const lat = currentPosition?.coords.latitude ?? 37.4799966;
    const lon = currentPosition?.coords.longitude ?? 126.9022916;
    const path = 'https://api.openweathermap.org/data/2.5/onecall';

    const search = queryString.stringify({
      appid: appId,
      lat,
      lon,
      lang: 'kr',
      exclude: 'hourly,minutely,current', // ÏãúÍ∞Ñ/Î∂Ñ Îã®ÏúÑ Î¶¨Ïä§Ìä∏ Ï†úÏô∏
      units: 'metric', // Ïò®ÎèÑÎ•º ÏÑ≠Ïî®Î°ú ÏÑ§Ï†ï
    });
    return `${path}?${search}`;
  }, [
    currentPosition?.coords.latitude,
    currentPosition?.coords.longitude,
  ]);

  const fetchWeather = async () => {
    try {
      const { data } = await axios.get(apiPath);

      return data.daily;
    } catch (e) {
      appendToast({
        type: 'error',
        message: 'ÎÇ†Ïî® Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§ÏßÄ Î™ªÌñàÏñ¥Ïöî',
      });
    }
  };

  const { data, isFetching } = useQuery<WeatherType[]>(
    [
      'getWeathers',
      currentPosition?.coords.latitude,
      currentPosition?.coords.longitude,
    ],
    fetchWeather,
  );

  return {
    weathers: data,
    isFetching,
  };
};
