type WeatherListItemProps = WeatherType;

export const WeatherListItem = (props: WeatherListItemProps) => {
  const { dt, weather, temp } = props;

  /**
   * referenced: https://openweathermap.org/weather-conditions#How-to-get-icon-URL
   */

  const weatherIcon = useMemo(() => {
    if (weather.length === 0) return null;
    const latestWeatherIconAlias = weather[0].icon;
    const matchedWeatherIconData = weatherIcons.find(
      (e) => e.alias === latestWeatherIconAlias.replace('n', 'd'),
    );

    if (!matchedWeatherIconData) return null;
    return matchedWeatherIconData.icon;
  }, [weather]);

  return (
    <Card>
      <Box
        display="flex"
        flexDirection="column"
        alignItems="center"
        py={1}
      >
        <Box mb={3} fontWeight={500} fontSize={2}>
          {dayjs.unix(dt).tz('Asia/Seoul').format('M/D(dd)')}
        </Box>

        {weather.length > 0 ? (
          <>
            <Box mb={2} fontSize={5}>
              {weatherIcon}
            </Box>

            {/* <Box mb={3} color="var(--color-grey-600)" fontSize={1}>
              ({weather[0].description})
            </Box> */}
          </>
        ) : null}

        <Box fontSize="1.375rem" fontWeight={500}>
          {Math.floor(typeof temp === 'number' ? temp : temp.day)}¬∫
        </Box>
      </Box>
    </Card>
  );
};
