import { useEffect, useState } from 'react';
import { useRecoilState } from 'recoil';
import { useToast } from '~/components/common';
import { geolocationIsDeniedState } from '~/states/geolocationState';

export const useGeolocation = () => {
  const [currentPosition, setCurrentPosition] =
    useState<GeolocationPosition | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [isDenied, setIsDenied] = useRecoilState(
    geolocationIsDeniedState,
  );
  const { appendToast } = useToast();

  const handleSuccess: PositionCallback = (position) => {
    setCurrentPosition(position);
    setIsLoading(false);
    setIsDenied(false);
  };

  const handleError: PositionErrorCallback = (positionError) => {
    if (isDenied) return;

    appendToast({
      type: 'error',
      message:
        'ÏúÑÏπò Ï†ïÎ≥¥ ÏàòÏßëÏùÑ ÌóàÏö©ÌïòÎ©¥ Îçî Ï†ïÌôïÌïú ÎÇ†Ïî®Î•º Î≥º Ïàò ÏûàÏñ¥Ïöî',
    });
    setIsDenied(true);
  };

  const fetchCurrentPosition = () => {
    if ('geolocation' in navigator) {
      setIsLoading(true);

      navigator.geolocation.getCurrentPosition(
        handleSuccess,
        handleError,
      );
    } else {
      setIsLoading(false);
      appendToast({
        type: 'error',
        message: 'ÎãπÏã†Ïùò ÏúÑÏπòÎ•º Ï∞æÏßÄ Î™ªÌñàÏñ¥Ïöî',
      });
    }
  };

  useEffect(() => {
    if (currentPosition) return;
    fetchCurrentPosition();
  }, []);

  return {
    isLoading,
    currentPosition,
    fetchCurrentPosition,
  };
};
